@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body{margin:0;font-family:'Lato', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-x:hidden}.header{margin-top:40px;margin-bottom:40px}.header .more{border:2px solid #EEF2F5;padding:5px 20px;border-radius:5px;background:#EEF2F5;outline:none;position:relative;margin-right:10px}.header .more:before{content:"\25A0 \25A0 \25A0";color:#9DADB9}.header .more:focus{border:2px solid rgba(157,173,185,0.2)}.header .add{border:2px solid #10CD45;padding:5px 20px;border-radius:5px;background:#10CD45;outline:none;position:relative;color:#fff}.header .add:focus{border:2px solid rgba(16,167,58,0.2)}

.icon-overview{vertical-align:middle;margin-right:10px}.icon-overview:before{display:inline-block;content:url(/metamins-test-task/static/media/overview.483b0c44.svg);font-size:14px}.icon-cloud{vertical-align:middle;margin-right:10px}.icon-cloud:before{display:inline-block;content:url(/metamins-test-task/static/media/cloud.654c351a.svg);font-size:14px}.icon-colors{vertical-align:middle;margin-right:10px}.icon-colors:before{display:inline-block;content:url(/metamins-test-task/static/media/colors.9a9bed41.svg);font-size:14px}.icon-experiments{vertical-align:middle;margin-right:10px}.icon-experiments:before{display:inline-block;content:url(/metamins-test-task/static/media/experiments.5bc962d4.svg);font-size:14px}.icon-security{vertical-align:middle;margin-right:10px}.icon-security:before{display:inline-block;content:url(/metamins-test-task/static/media/security.ffb3555b.svg);font-size:14px}.icon-sketch{vertical-align:middle;margin-right:10px}.icon-sketch:before{display:inline-block;content:url(/metamins-test-task/static/media/sketch.fdf90b46.svg);font-size:14px}.icon-test{vertical-align:middle;margin-right:10px}.icon-test:before{display:inline-block;content:url(/metamins-test-task/static/media/test.258e5131.svg);font-size:14px}.icon-ownership{vertical-align:middle;margin-right:10px}.icon-ownership:before{display:inline-block;content:url(/metamins-test-task/static/media/ownership.75db29be.svg);font-size:14px}.icon-right{vertical-align:middle;margin-right:10px}.icon-right:before{display:inline-block;content:url(/metamins-test-task/static/media/icon-right.7a32f4fa.svg);font-size:14px}.icon-left{vertical-align:middle;margin-right:10px}.icon-left:before{display:inline-block;content:url(/metamins-test-task/static/media/icon-left.0a11ad12.svg);font-size:14px}.icon-burger-menu{display:none;width:30px;height:30px;position:relative;transform:rotate(0deg);transition:.5s ease-in-out;margin-top:7px;cursor:pointer}@media (max-width: 768px){.icon-burger-menu{display:block}}.icon-burger-menu span{display:block;position:absolute;height:3px;width:100%;background:rgba(255,255,255,0.5);border-radius:9px;opacity:1;left:0;transform:rotate(0deg);transition:.25s ease-in-out}.icon-burger-menu span:nth-child(1){top:0px}.icon-burger-menu span:nth-child(2){top:10px}.icon-burger-menu span:nth-child(3){top:20px}.icon-burger-menu.open span:nth-child(1){top:10px;transform:rotate(135deg)}.icon-burger-menu.open span:nth-child(2){opacity:0;left:-60px}.icon-burger-menu.open span:nth-child(3){top:10px;transform:rotate(-135deg)}

.menu{padding:20px;margin:0;list-style-type:none}@media (max-width: 768px){.menu{display:none}}.menu-item{margin-bottom:15px}@media (min-width: 768px) and (max-width: 1110px){.menu-item{display:flex;justify-content:center}.menu-item span{display:none}}.menu-link{color:#fff;text-decoration:none}.menu-link.active{font-weight:700}.menu-link:hover{color:rgba(255,255,255,0.8)}

.sidebar{width:100%;height:100%;background:linear-gradient(175.93deg, #5F00D8 -10.23%, #B200F1 117.03%)}.sidebar .logo-container{padding:15px;width:90%;margin:0 auto;border-bottom:1px solid rgba(255,255,255,0.2)}

.global-container{height:100vh}.row-height{height:100%}@media (max-width: 768px){.row-height{height:auto}}

.table thead tr th{white-space:nowrap}.table .item-phone{white-space:nowrap}.table .item-owner{white-space:nowrap}.table .item-profits{color:#00CA39}.table .item-profits:before{content:"$"}.table .item-losses{color:#E44067}.table .item-losses:before{content:"$"}

.pagination .numberPage{width:45px;margin:5px}.pagination .numberPage .input{width:100%;padding:5px 10px;border:2px solid #EEF2F5;border-radius:5px;background:#EEF2F5;color:#000;outline:none}.pagination .numberPage .input:focus{border:2px solid rgba(157,173,185,0.2)}.pagination .devider{margin-left:10px;margin-right:10px}.pagination .arrow-right{margin-left:10px;cursor:pointer}.pagination .arrow-right .icon-right{margin-right:0}.pagination .arrow-left{cursor:pointer}

